.woocommerce-form-login__rememberme {
  display: block;
}

.w3l_logo span {
  display: block;
  margin-bottom: 10px;
  text-align: center;
}

.w3l_logo span:last-of-type {
  font-size: 14px;
}

.w3view-cart {
  position: static;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  transition: all .3s;
  color: white;
}

.w3view-cart:hover {
  background-color: #d24456;
}

.w3view-cart:active,
.w3view-cart:focus {
  text-decoration: none;
}

.w3view-cart .count {
  margin-right: 4px;
  font-size: 10px;
  font-weight: bold;
  color: white;
}

.minicart-box {
  position: fixed;
  top: 30px;
  right: 170px;
  z-index: 1000;
}

.minicart-box__content {
  position: absolute;
  top: 60px;
  left: -90px;
  display: none;
  width: 240px;
  padding: 15px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, .4);
  background-color: white;
  border-radius: 6px;
}
.address {
  li {
    display: flex;
    i {
      margin-right: 10px;
    }
  }
}
.page-content {
  padding: 40px 0;
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin-top: 20px;
    margin-bottom: 10px;
  }
  
  p {
    margin-bottom: 10px;
  }
  ul {
    margin-bottom: 30px;
    li {
      margin-left: 40px;
    }
  }
  blockquote {
    margin-bottom: 40px;
  }
}
